function App() {
  const [card, setCard] = useState(sourceData[0].rule);
  const [cardList, setCardList] = useState(sourceData);

  const nextCard = () => {
    var card = cardList[Math.floor(Math.random() * cardList.length)];
    if (card === undefined) {
      setCard('Koniec :) kliknij se reset');
      return;
    }
    setCard(card.rule);
    setCardList(cardList.filter((x) => x.id !== card.id));
  };

  const reset = () => {
    setCardList(sourceData);
    nextCard();
  };

  return (
    <Flex gap="center" align="center" vertical style={{ width: '100%' }}>
      <main>
        <Space direction="vertical">
          <Title style={{ textAlign: 'center' }}>Boruta Christmas Game</Title>
          <Card style={{ height: 300 }}>
            <Title level={2} style={{ textAlign: 'left' }}>
              {card}
            </Title>
          </Card>
          <Button
            onClick={() => nextCard()}
            type="primary"
            size="large"
            icon={<RocketOutlined />}
            block
          >
            Next
          </Button>
          <Button
            onClick={() => reset()}
            type="primary"
            size="large"
            icon={<RocketOutlined />}
            block
          >
            Reset
          </Button>
        </Space>
      </main>
    </Flex>
  );
}

export default App;
